        font-size: 1.1rem;
        font-weight: 600;
        color: #FFFFFF; }
        @media (max-width: 576px) {
          header .header--logo .logo {
            font-size: 1rem; } }
    @media only screen and (max-width: 912px) {
      header .header--menu {
