            font-weight: 400; }
            @media (max-width: 576px) {
              header .header--user-action .user-point .point .p {
                font-size: 0.75rem; } }
      header .header--user-action .user-dropdown {
        position: relative;
        display: inline-block; }
        @media (max-width: 576px) {
          header .header--user-action .user-dropdown {
            display: none; } }
        header .header--user-action .user-dropdown button {
          background-color: transparent;
          display: flex;
