.btn-create-task {
  padding: 15px 18px;
  font-size: 1rem;
  font-weight: 400;
  background-color: #2D2D3A;
  color: #FFFFFF;
  border-radius: 15px;
  transition: background-color 250ms; }
  .btn-create-task i {
    margin-right: 10px; }
  .btn-create-task:hover {
    background-color: #4e4e58; }

.btn-task-view {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 100%;
  padding: 10px 0;
  color: #FFFFFF;
  border: 2px solid #4447E2 !important;
  border-radius: 15px;
  transition: 300ms all; }
  .btn-task-view:hover {
    background-color: #4447E2;
    color: #FFFFFF;
    border: 0; }

.create-task {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px; }
  .create-task .task-title {
    font-size: 1.6rem;
    font-weight: 400;
    color: #FFFFFF; }

.tasks {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(17rem, 1fr));
  grid-template-rows: repeat(200px 1fr 1fr);
  grid-gap: 15px; }
  .tasks .task-card {
    display: flex;
    flex-direction: column;
    background-color: #1C1C24;
    color: #FFFFFF;
    border-radius: 15px;
    padding: 20px; }
    .tasks .task-card .card-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px; }
      .tasks .task-card .card-header .user-info {
        display: flex;
        align-items: center;
        justify-content: center; }
        .tasks .task-card .card-header .user-info .user-photo {
          margin-right: 10px;
          position: relative; }
          .tasks .task-card .card-header .user-info .user-photo .u-photo {
            width: 50px;
            height: 50px;
            object-fit: cover;
            border-radius: 50%; }
          .tasks .task-card .card-header .user-info .user-photo .action {
            position: absolute;
            width: 16px;
            height: 16px;
            left: 35px;
            top: 32px;
            border-radius: 50%;
            background-color: #1C1C24; }
        .tasks .task-card .card-header .user-info .u-info {
          display: flex;
          flex-direction: column; }
          .tasks .task-card .card-header .user-info .u-info .username {
            font-size: 1rem;
            font-weight: 400;
            color: #FFFFFF; }
          .tasks .task-card .card-header .user-info .u-info .action {
